<template>
  <div class='about'>
    <div id='particles'></div>
    <div class='table-cell'>
      <div class='text'>
        <router-link :to="{ path: '/' }" title="Home" class="info"><img src="../assets/images/home.png" alt="Home" height="24px" width="24px" /></router-link>
        <h1>lovely pessimism</h1>
        <p class='about'>I gathered the best motivational quotes. On social media, your friends are always posting the best inspirational quotes. Staying on the top is hard. This little collection is going to help you to get ahead of your friends in optimism and positivity. With hard work you can be like this:</p>
        <img src="../assets/images/sad.png" />
        <br><br>
        <router-link :to="{ path: '/terms' }" title="Terms of Use">Terms of Use</router-link> | <a href="mailto:lovelypessimism@gmail.com" title="Contact">Contact</a> | <a href="https://www.facebook.com/lovelypessimism/" title="Facebook">Facebook</a>
        <br>© 2017 lovelypessimism.com
      </div>
    </div>
  </div><!-- FD055E -->
</template>


<script>
export default {
  name: 'About',
  data () {
    return {

    }
  },
  mounted () {
  	this.$nextTick(() => {
      this.initParticleJS()
    })
  },
  methods: {
  	initParticleJS () {
      particlesJS('particles', {
        "particles": {
          "number": {
            "value": 160,
            "density": {
              "enable": true,
              "value_area": 800
            }
          },
          "color": {
            "value": "#FFFEFE"
          },
          "shape": {
            "type": "heart",
            "stroke": {
              "width": 0,
              "color": "#000000"
            },
            "polygon": {
              "nb_sides": 5
            },
            "image": {
              "src": require("../assets/whiteHeart.svg"),
              "width": 100,
              "height": 100
            }
          },
          "opacity": {
            "value": 1,
            "random": true,
            "anim": {
              "enable": true,
              "speed": 1,
              "opacity_min": 0,
              "sync": false
            }
          },
          "size": {
            "value": 10,
            "random": true,
            "anim": {
              "enable": false,
              "speed": 4,
              "size_min": 0.5,
              "sync": false
            }
          },
          "line_linked": {
            "enable": false,
            "distance": 150,
            "color": "#ffffff",
            "opacity": 0.4,
            "width": 1
          },
          "move": {
            "enable": true,
            "speed": 1,
            "direction": "none",
            "random": true,
            "straight": false,
            "out_mode": "out",
            "bounce": false,
            "attract": {
              "enable": false,
              "rotateX": 600,
              "rotateY": 600
            }
          }
        },
        "interactivity": {
          "detect_on": "canvas",
          "events": {
            "onhover": {
              "enable": true,
              "mode": "bubble"
            },
            "onclick": {
              "enable": true,
              "mode": "repulse"
            },
            "resize": true
          },
          "modes": {
            "grab": {
              "distance": 400,
              "line_linked": {
                "opacity": 1
              }
            },
            "bubble": {
              "distance": 250,
              "size": 0,
              "duration": 2,
              "opacity": 0,
              "speed": 3
            },
            "repulse": {
              "distance": 400,
              "duration": 0.4
            },
            "push": {
              "particles_nb": 4
            },
            "remove": {
              "particles_nb": 2
            }
          }
        },
        "retina_detect": true
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.about {
  width: 100%;
  height: 100%;
  display: table;
  text-align: center;
}

.table-cell {
    display: table-cell;
    vertical-align: middle !important;
    text-align: center;
    padding: 10px;
}

#particles {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
}

.info {
  position: absolute;
  top: 10px;
  right: 10px;
  opacity: 0.2;
  filter: alpha(opacity=20); /* For IE8 and earlier */
}

.info:hover {
  opacity: 0.4;
  filter: alpha(opacity=40);
}

.text {
  position: relative;
  color: #ffffff;
  max-width: 720px;
  margin: 0 auto;
  padding: 2em 3em;
  background: #980339;
  background: rgba(0, 0, 0, 0.4);
  text-shadow: 0px 0px 2px #131415;
  font-family: 'Open Sans', sans-serif;
}

h1 {
  font-family: 'Caveat Brush', cursive;
	font-size: 3em;
	font-weight: normal;
  margin-top: 0;
  margin-bottom: 0;
}

.yellow {
  color: #fcb042;
}

a,
a:visited {
  margin-top: 0;
	color: #FCB042;
  font-family: 'Arima Madurai', cursive;
	transition: 0.25s;
}

a:hover,
a:focus {
	color:#FFE29F;
}

p.about {
  font-family: 'Arima Madurai', cursive;
  font-size: 1.4em;
}


</style>



