<template>
  <div class='quote'>

    <div id='particles'></div>
    <div class='table-cell'>
      <div class='text'>
        <router-link :to="{ path: '/about' }" title="Information" class="info"><img src="../assets/images/info.png" alt="Information" height="24px" width="24px" /></router-link>
        <!-- Go to www.addthis.com/dashboard to customize your tools -->
        <div class="addthis_toolbox addthis_inline_share_toolbox"></div>
        <p class='quotes'>“{{ quote }}” <span class='nowrap'>― <a :href="fullAmazon">{{ person }}</a></span></p>
        <p><button class='button-next' v-on:click='randomQuote'>¯\_(ツ)_/¯</button></p>
      </div>
    </div>
  </div><!-- FD055E -->
</template>


<script>
export default {
  name: 'Quote',
  data () {
    return {
      randomNum: 0,
      record: null,
      quote: '',
      person: '',
      last: 10,
      url: '',
      about: '',
      baseAmazon: "https://www.amazon.com/gp/search?ie=UTF8&tag=lovelypessimi-20&linkCode=ur2&camp=1789&creative=9325&index=aps&keywords=",
      fullAmazon: "",
      encodedKeyword: "",
    }
  },
  created () {
    this.about = { path: '/about'};
    if (typeof this.$route.params.id == 'undefined') {
      this.last = this.$quotations().last().id;
      this.randomNum = Math.floor((Math.random() * this.last) + 1);
      this.quote = "Abandon all hope, ye who enter here.";
      this.person = "Dante Alighieri";
      this.url = window.location.href;
    }
    else {
      this.last = this.$quotations().last().id;
      this.randomNum = Math.floor((Math.random() * this.last) + 1);
      if (isNaN(this.$route.params.id)) {
        this.$router.push({ path: '/'});
        this.quote = "Abandon all hope, ye who enter here.";
        this.person = "Dante Alighieri";
        this.url = window.location.href;
      }
      else {
        this.randomNum = parseInt(this.$route.params.id, 10);
        this.record = this.$quotations({'id': this.randomNum}).first();
        this.quote = this.record.quote;
        this.person = this.record.person;
        this.url = window.location.href;
      }

    }

    this.encodedKeyword = encodeURIComponent(this.person);
    this.fullAmazon = this.baseAmazon + this.encodedKeyword;

    // og title, description, url
    document.getElementsByTagName("meta")[7].content = this.$appName;
    document.getElementsByTagName("meta")[8].content = this.quote;
  },
  mounted () {
  	this.$nextTick(() => {
      this.initParticleJS()
    })
  },
  methods: {
  	initParticleJS () {
      particlesJS('particles', {
        "particles": {
          "number": {
            "value": 160,
            "density": {
              "enable": true,
              "value_area": 800
            }
          },
          "color": {
            "value": "#FFFEFE"
          },
          "shape": {
            "type": "heart",
            "stroke": {
              "width": 0,
              "color": "#000000"
            },
            "polygon": {
              "nb_sides": 5
            },
            "image": {
              "src": require("../assets/whiteHeart.svg"),
              "width": 100,
              "height": 100
            }
          },
          "opacity": {
            "value": 1,
            "random": true,
            "anim": {
              "enable": true,
              "speed": 1,
              "opacity_min": 0,
              "sync": false
            }
          },
          "size": {
            "value": 10,
            "random": true,
            "anim": {
              "enable": false,
              "speed": 4,
              "size_min": 0.5,
              "sync": false
            }
          },
          "line_linked": {
            "enable": false,
            "distance": 150,
            "color": "#ffffff",
            "opacity": 0.4,
            "width": 1
          },
          "move": {
            "enable": true,
            "speed": 1,
            "direction": "none",
            "random": true,
            "straight": false,
            "out_mode": "out",
            "bounce": false,
            "attract": {
              "enable": false,
              "rotateX": 600,
              "rotateY": 600
            }
          }
        },
        "interactivity": {
          "detect_on": "canvas",
          "events": {
            "onhover": {
              "enable": true,
              "mode": "bubble"
            },
            "onclick": {
              "enable": true,
              "mode": "repulse"
            },
            "resize": true
          },
          "modes": {
            "grab": {
              "distance": 400,
              "line_linked": {
                "opacity": 1
              }
            },
            "bubble": {
              "distance": 250,
              "size": 0,
              "duration": 2,
              "opacity": 0,
              "speed": 3
            },
            "repulse": {
              "distance": 400,
              "duration": 0.4
            },
            "push": {
              "particles_nb": 4
            },
            "remove": {
              "particles_nb": 2
            }
          }
        },
        "retina_detect": true
      });
    },
    randomQuote () {
      this.randomNum = Math.floor((Math.random() * this.last) + 1);
      this.$router.push({ path: '/quote/' + this.randomNum});
      this.record = this.$quotations({'id': this.randomNum}).first();
      this.quote = this.record.quote;
      this.person = this.record.person;
      this.url = window.location.href;

      this.encodedKeyword = encodeURIComponent(this.person);
      this.fullAmazon = this.baseAmazon + this.encodedKeyword;

      // og title, description, url
      document.getElementsByTagName("meta")[7].content = this.$appName;
      document.getElementsByTagName("meta")[8].content = this.quote;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.quote {
  width: 100%;
  height: 100%;
  display: table;
  text-align: center;
}

.table-cell {
    display: table-cell;
    vertical-align: middle !important;
    text-align: center;
    padding: 10px;
}

#particles {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
}

.info {
  position: absolute;
  top: 10px;
  right: 10px;
  opacity: 0.2;
  filter: alpha(opacity=20); /* For IE8 and earlier */
}

.info:hover {
  opacity: 0.4;
  filter: alpha(opacity=40);
}

.text {
  position: relative;
  color: #ffffff;
  max-width: 720px;
  margin: 0 auto;
  padding: 2em 3em;
  background: #980339;
  background: rgba(0, 0, 0, 0.4);
  text-shadow: 0px 0px 2px #131415;
  font-family: 'Open Sans', sans-serif;
}

h1 {
  font-family: 'Caveat Brush', cursive;
	font-size: 3em;
	font-weight: normal;
  margin-top: 0;
  margin-bottom: 0;
}

.yellow {
  color: #fcb042;
}

a,
a:visited {
  margin-top: 0;
	color: #FCB042;
  font-family: 'Arima Madurai', cursive;
	transition: 0.25s;
}

a:hover,
a:focus {
	color:#FFE29F;
}

p.quotes, p.quoted {
  font-family: 'Arima Madurai', cursive;
  font-size: 1.4em;
}

p.quoted {
  margin-top: 0;
  padding-top: 0;
}

p.emoticon {
  font-size: 1.6em;
}

button::-moz-focus-inner {
  border: 0;
}

.button-next {
  text-align: center;
  cursor: pointer;
  width: 100%;
  font-size:24px;
  margin: 0;
  padding:10px 30px;
  outline: none;
  color: #ffffff;
  background: #c16888;
  background: rgba(256, 256, 256, 0.4);
  border: none;
  border-radius:5px;
  box-shadow: 0 5px #95a5a6;

    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none;
}

.button-next:hover{
  background: #b74f75;
  background: rgba(256, 256, 256, 0.3);
}

.button-next:active {
  background: #b74f75;
  background: rgba(256, 256, 256, 0.3);
  box-shadow: 0 5px #95a5a6;
  transform: translateY(4px);
}

@media only screen and (min-width: 500px) {
  .nowrap {
      white-space: nowrap;
  }
}


</style>



