import Vue from 'vue'
import Router from 'vue-router'
import Quote from '@/components/Quote'
import About from '@/components/About'
import Terms from '@/components/Terms'

Vue.use(Router)

export default new Router({
  //mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Quote
    },
    {
      path: '/quote',
      redirect: '/'
    },
    {
      path: '/quote/:id',
      name: 'quote',
      component: Quote
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/terms',
      name: 'terms',
      component: Terms
    }
  ]
})



